<template>
  <div id="box_div">
    <h2>账号列表</h2>
    <div id="Uslist_div"
         v-show="UslistShow">
      <el-table ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
        <!-- @selection-change="handleSelectionChange" -->
        <el-table-column type="selection"
                         width="100">
        </el-table-column>

        <el-table-column label="账号"
                         width="300">
          <template slot-scope="scope">{{ scope.row.account }}</template>
        </el-table-column>

        <el-table-column prop="userGroup"
                         label="用户组"
                         width="300">
        </el-table-column>
        <el-table-column prop="ctime"
                         label="创建时间"
                         show-overflow-tooltip>
          <template slot-scope="scope">{{ scope.row.ctime}}</template>
        </el-table-column>
        <el-table-column prop="ae"
                         label="操作"
                         show-overflow-tooltip>
          <template slot-scope="scope">
            <el-button size="small"
                       @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button @click.native.prevent="deleteRow(scope.$index, tableData)"
                       type="danger"
                       size="small">
              删除
            </el-button>
          </template>

        </el-table-column>

      </el-table>

      <div style="margin-top: 20px">
        <el-button @click="toggleSelection()"
                   type="danger">批量删除</el-button>
        <el-button type="primary"
                   @click="toggleSelection()">
          取消选择
        </el-button>
      </div>

      <div class="block"
           style="margin: 10px auto;">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage4"
                       :page-sizes="[3, , 6, 10]"
                       :page-size="pageSize"
                       layout="total, sizes, prev, pager, next, jumper"
                       :total="total">
        </el-pagination>
      </div>

    </div>
    <div id="handleEdit"
         v-show="editShow">
      <p>
        <label>账号</label>
        <el-input v-model="ninput"></el-input>
      </p>
      <p>
        <label>用户组</label>
        <el-select v-model="value"
                   placeholder="请选择用户组">
          <el-option v-for="item in products"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
            <span style="float: left">{{ item.label }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
          </el-option>
        </el-select>
      </p>
      <el-button type="primary"
                 @click="save()"
                 id="save">保存</el-button>
    </div>

  </div>
</template>

<script>
import { userlist, userdel, batchdel, edit } from '@/API/apis'//获取账号列表
import { getchtime } from '@/utils/gettime'//时间 

export default {

  data () {
    return {
      multipleSelection: [],
      currentPage: 1,
      pageSize: 3,

      //翻页
      currentPage4: 1,
      tableData: [],
      total: 0,
      ids: [],
      id: '',
      //显示隐藏
      editShow: false,
      UslistShow: true,
      //编辑
      products: [
        {
          value: '超级管理员',
          label: '超级管理员'
        },
        {
          value: '普通管理员',
          label: '普通管理员'
        },
      ],
      value: '',
      ninput: '',
    }
  },
  created () {
    this.List()
  },
  methods: {
    List () {
      userlist(this.currentPage, this.pageSize).then(res => {
        this.total = res.data.total
        this.arr = res.data.data
        for (const obj of this.arr) {
          obj.ctime = getchtime(obj.ctime)
        }
        this.tableData = this.arr
      })
    },
    //翻页
    handleSizeChange (pageSize) {
      this.pageSize = pageSize
      this.List()
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.List()
    },
    // 删除
    deleteRow (index) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        userdel(this.tableData[index].id, 1).then(res => {
          if (res.data.code == 0) {
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
            this.List()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },


    //编辑
    handleEdit (index, row) {
      this.editShow = true
      this.UslistShow = false
      this.id = row.id
      this.ninput = row.account
      this.value = row.userGroup
    },
    //保存编辑  
    save () {
      edit(this.id, this.ninput, this.value).then(res => {
        if (res.data.code == 0) {
          this.editShow = false
          this.UslistShow = true
          this.$message({
            message: res.data.msg,
            type: 'success'
          });
          this.List()
        }
      })
    },
    // 批量删除
    handleSelectionChange (val) {
      var Val = val.map(item => { return item.id })
      this.ids = JSON.stringify(Val)
    },
    toggleSelection () {

      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        batchdel(this.ids).then(res => {
          if (res.data.code == 0) {
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
            this.List()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
  },
}
</script>

<style lang="less" scoped>
#box_div {
  position: relative;
  #handleEdit {
    width: 380px;
    height: 250px;
    position: absolute;
    top: 200px;
    left: 30%;
    background-color: white;
    border: 1px solid #ccc;
    padding: 30px;
    border-radius: 10px;
    p {
      margin-bottom: 15px;
      label {
        display: inline-block;
        width: 60px;
        text-align: right;
        margin-right: 20px;
        color: #808080;
      }
      .el-input,
      .el-select {
        width: 75%;
      }
    }
    #save {
      width: 100%;
    }
  }
}
</style>